import React, { Component } from 'react';
import './Calculator.css';

class Calculator extends Component {
    state = {
        enter: '',
        result: 0,
        toggleValue: true
    };

    visible = (e) => {

        const value = e.target.name;

        const value1 = this.state.enter.concat(value);
        this.setState({ enter: value1, toggleValue: true, result: 0 });


    };

    resultValue = () => {
        try {
            const evaluate = eval(this.state.enter);
            this.setState({ enter: '', result: evaluate, toggleValue: !this.state.toggleValue });
        } catch (error) {
            alert("Invalid Mathematical Equation");
        }


    };

    clearCal = () => {
        this.setState({ result: 0, enter: '' });
    };

    backspace = () => {
        let enter = this.state.enter;
        enter = enter.trim();
        enter = enter.substr(0, enter.length - 1);
        this.setState({ enter: enter });
    };

    render() {

        return (
            <div className="mainContainer">
                <div className="flexContainer">
                    <div className="result">{this.state.toggleValue === true ? this.state.enter : this.state.result}</div>
                </div>
                <div className="flexContainer">
                    <button className="button darkGray" onClick={this.clearCal}>AC</button>
                    <button className="button darkGray" onClick={this.backspace}>&larr;</button>
                    <button className="button darkGray" name="%" onClick={this.visible}>%</button>
                    <button className="button orange" name="/" onClick={this.visible}>/</button>
                </div>
                <div className="flexContainer">
                    <button name="7" className="button" onClick={this.visible}>7</button>
                    <button name="8" className="button" onClick={this.visible}>8</button>
                    <button name="9" className="button" onClick={this.visible}>9</button>
                    <button name="*" className="button orange" onClick={this.visible}>*</button>
                </div>
                <div className="flexContainer">
                    <button name="4" className="button" onClick={this.visible}>4</button>
                    <button name="5" className="button" onClick={this.visible}>5</button>
                    <button name="6" className="button" onClick={this.visible}>6</button>
                    <button name="-" className="button orange" onClick={this.visible}>-</button>
                </div>
                <div className="flexContainer">
                    <button name="1" className="button" onClick={this.visible}>1</button>
                    <button name="2" className="button" onClick={this.visible}>2</button>
                    <button name="3" className="button" onClick={this.visible}>3</button>
                    <button name="+" className="button orange" onClick={this.visible}>+</button>
                </div>
                <div className="flexContainer">
                    <button name="0" className="button zero" onClick={this.visible}>0</button>
                    <button name="." className="button" onClick={this.visible}><b>.</b></button>
                    <button className="button orange doubleWidth" onClick={this.resultValue}>=</button>
                </div>

            </div>
        );
    }
}

export default Calculator;
